
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'

import upload from './Plug/upload.vue'
export default {
directives: {
TransferDom
},
data(){
return {
guige:[],
name:"",
price:"",
type:"",
dec:"",
goods_id:null,
title1: '类型',
list1: [],
value1: [],
downdelete:false,
deling:false
}
},
mounted(){
this.goods_id=this.$route.query.goods_id
this.getTheGoodInfo()
this.getTypes()
},
methods:{
getTheGoodInfo(){
this.$http.get("getGoodsItem?goods_id="+this.goods_id)
.then(
response=>{
console.log(response.data)
var the_item = response.data[0].fields
this.guige = JSON.parse(the_item.standards)
this.name =  the_item.name
this.price = the_item.price
this.dec   = the_item.describe
this.value1.push(the_item.type)
console.log(the_item.type)
},response=>{

console.log("error")
})
},
changes(value){
console.log(value)
this.$http.post('http://localhost:8000/updateItem',
{
"goods_id":this.goods_id,
"type":this.value1[0]

}).then(response => {
console.log("sucess")
}, response => {
console.log("error");
});
},
getTypes(){
this.$http.get('http://localhost:8000/getGoodsTypes').then(response => {
this.list1=[this.querySetToArray(response.data)]
console.log(this.list1)




}, response => {
console.log("error");
});
},
querySetToArray(obj){
var newObj=[]
for( var i=0;i<obj.length;i++){
newObj.push(
{
value:obj[i].pk,
name:obj[i].fields.name
})
}
return newObj
},
deleteGoods(){
this.deling=true
this.$http.get('http://localhost:8000/deleteGoods?goods_id='+this.goods_id).then(
response => {
this.deling=false
this.$router.replace({path:"goods"})
},
response => {
console.log("error");
}
);
}

},
components: {
Group,
Cell,
CellBox,
upload,
XTextarea
,XButton,
PopupPicker,
Confirm,
Loading

}
}



